import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Route, Routes, useLocation } from 'react-router-dom';
import Navbar from './components/Navbar';
import Home from './components/Home';
import About from './components/About';
import Experience from './components/Experience';
import Projects from './components/Projects';
import Contact from './components/Contact';
import Loader from './components/Loader';
import ScrollUpButton from './components/ScrollUpButton';
import './App.css';
import { DarkModeProvider } from './context/DarkModeContext';
// import NetflixHome from './components/netflix-components/NetflixHome';
import ConditionalCSSLoader from './ConditionalCSSLoader';
import InvestmentCalculator from './components/projects/investment-calculator/InvestmentCalculator';

function AppContent() {
  const location = useLocation();

  return (
    <>
      <ConditionalCSSLoader />
      {location.pathname !== '/netflix' && <Navbar /> }
      <ScrollUpButton />
      <Routes>
        <Route path="/" element={<Home />} />
        <Route path="/#about" element={<About />} />
        <Route path="/#experience" element={<Experience />} />
        <Route path="/#projects" element={<Projects />} />
        <Route path="/#contact" element={<Contact />} />
        {/* <Route path="/netflix" element={<NetflixHome />} /> */}
        <Route path="/investment-calculator" element={<InvestmentCalculator />} />
      </Routes>
    </>
  );
}

function App() {
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    setTimeout(() => {
      setLoading(false);
    }, 2000); // Simulate loading time
  }, []);

  return (
    <DarkModeProvider>
      {loading ? (
        <Loader />
      ) : (
        <Router>
          <AppContent />
        </Router>
      )}
    </DarkModeProvider>
  );
}

export default App;
