import React, { useState } from 'react';
import { Header } from './Header';
import { UserInput } from './UserInput';
import { Results } from './Results';
import { InvestmentGraph } from './InvestmentGraph';
import { GraphSelector } from './GraphSelector';
import { calculateInvestmentResults } from './investment';
import './InvestmentCalculator.css';

function InvestmentCalculator() {
  const [userInput, setUserInput] = useState({
    initialInvestment: 0,
    annualInvestment: 0,
    expectedReturn: 0,
    duration: 1,
  });

  const [selectedGraph, setSelectedGraph] = useState('LineChart');

  const handleInputChange = (field, value) => {
    setUserInput((prev) => ({
      ...prev,
      [field]: value,
    }));
  };

  return (
    <>
      <Header />
      <UserInput userInput={userInput} onChange={handleInputChange} />
      <GraphSelector
        selectedGraph={selectedGraph}
        onChange={setSelectedGraph}
      />
      <Results input={userInput} />
      <InvestmentGraph
        data={calculateInvestmentResults(userInput)}
        graphType={selectedGraph}
      />
    </>
  );
}

export default InvestmentCalculator;
