import React, { useContext, useState } from 'react';
import { useSwipeable } from 'react-swipeable';
import './Experience.scss';
import DarkModeContext from './../context/DarkModeContext';

const experiences = [
  {
    id: 'pills-AmExGBT',
    company: 'American Express GBT',
    location: 'Benguluru',
    period: 'Oct 2024 - Present',
    details: [
      "Developing and maintaining a Developer Portal (API Portal) using Drupal to enhance API discovery and user experience.",
      "Implementing customized features, including user authentication, API documentation, and subscription management for API consumers.",
      "Designing and developing interactive user interfaces to simplify API onboarding and enhance usability.",
      "Integrating API testing tools, enabling users to explore and test APIs directly through the portal.",
      "Collaborating with cross-functional teams to gather requirements and align portal features with business goals.",
      "Ensuring portal security by implementing robust access control, data encryption, and secure API gateways.",
      "Monitoring performance and implementing optimizations to ensure scalability and reliability.",
      "Creating comprehensive technical documentation for portal functionality and maintenance processes."
    ],
  },
  {
    id: 'pills-indegene',
    company: 'Indegene',
    location: 'Benguluru',
    period: 'Aug 2023 - Aug 2024',
    details: [
      'Worked on SEO for Drupal application using Metatag, Simple XML Sitemap, Redirect and Pathauto modules.',
      'Improved content strategy and better digital marketing strategy using Adobe analytics.',
      'Dynamic pages creation using custom block types in node layout.',
      'Worked on Health Domain Drupal Websites for various countries.',
    ],
  },
  {
    id: 'pills-trinity',
    company: 'Trinity',
    location: 'Benguluru',
    period: 'July 2021 - Aug 2023',
    details: [
      'Develop and maintain high-quality web applications using Drupal.',
      'Design and implement custom modules and functionalities to meet project requirements.',
      'Integrate third-party APIs for enhanced functionality and data exchange.',
      'Optimize website performance through caching, database tuning, and code optimization.',
      'Collaborate with front-end developers and designers to ensure seamless integration of front-end and back-end components.',
      'Conduct code reviews and provide technical guidance to junior developers.',
      'Troubleshoot and resolve issues related to website functionality, performance, and security.',
    ],
  },
  {
    id: 'pills-creatisoul',
    company: 'Creatisoul',
    location: 'Benguluru',
    period: 'Oct 2020 - July 2021',
    details: [
      'Developed user interaction websites using HTML, CSS, JavaScript, jQuery, and Drupal.',
      'Ensured cross-device compatibility and responsive design principles.',
      'Collaborated with the team to deliver projects within deadlines.',
      'Assisted in troubleshooting and resolving website issues.',
    ],
  },
];

const Experience = () => {
  const { isDarkMode } = useContext(DarkModeContext);
  const [activeTab, setActiveTab] = useState(0);

  const handleTabChange = (index) => {
    setActiveTab(index);
    const tabButton = document.querySelectorAll('.nav-item .cta-btn')[index];
    if (tabButton) {
      tabButton.click();
    }
  };

  const swipeHandlers = useSwipeable({
    onSwipedLeft: () => handleTabChange((activeTab + 1) % experiences.length),
    onSwipedRight: () => handleTabChange((activeTab - 1 + experiences.length) % experiences.length),
    trackMouse: true,
  });

  return (
    <section id="experience" className={`experience-section ${isDarkMode ? 'dark-mode' : ''}`}>
      <div className="container">
        <h2 className="mb-5 maven-font heading text-center">Experience</h2>
        <div className="scroll-indicator">➔</div>
        <ul className="nav nav-pills mb-3" id="pills-tab" role="tablist">
          {experiences.map((exp, index) => (
            <li className="nav-item" role="presentation" key={index}>
              <button
                className={`cta-btn ${activeTab === index ? 'active' : ''}`}
                id={`${exp.id}-tab`}
                data-bs-toggle="pill"
                data-bs-target={`#${exp.id}`}
                type="button"
                role="tab"
                aria-controls={exp.id}
                aria-selected={activeTab === index}
                onClick={() => setActiveTab(index)}
              >
                {exp.company}<span>, {exp.location}</span>
                {/* <div>
                  
                </div> */}
                <div>
                  <span>{exp.period}</span>
                </div>
              </button>

            </li>
          ))}
        </ul>
        <div className="tab-content" id="pills-tabContent" {...swipeHandlers}>
          {experiences.map((exp, index) => (
            <div
              className={`tab-pane fade ${activeTab === index ? 'show active' : ''}`}
              id={exp.id}
              role="tabpanel"
              aria-labelledby={`${exp.id}-tab`}
              key={index}
            >
              <p className="experience-year">{exp.period}</p>
              <ul>
                {exp.details.map((detail, i) => (
                  <li key={i}>{detail}</li>
                ))}
              </ul>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
};

export default Experience;
