import React from 'react';
import SliderInput from './SliderInput';

export function UserInput({ userInput, onChange }) {
  return (
    <div id="user-input">
      <h3 className="center">Set Your Investment Details</h3>
      <div className="input-group">
        <SliderInput
          label="Initial Investment (₹)"
          id="initialInvestment"
          value={userInput.initialInvestment}
          min="0"
          max="1000000"
          step="1000"
          onChange={(value) => onChange('initialInvestment', value)}
        />
        <SliderInput
          label="Annual Investment (₹)"
          id="annualInvestment"
          value={userInput.annualInvestment}
          min="0"
          max="500000"
          step="1000"
          onChange={(value) => onChange('annualInvestment', value)}
        />
        <SliderInput
          label="Expected Return Rate (p.a.) (%)"
          id="expectedReturn"
          value={userInput.expectedReturn}
          min="1"
          max="20"
          step="0.1"
          onChange={(value) => onChange('expectedReturn', value)}
        />
        <SliderInput
          label="Time Period (Years)"
          id="duration"
          value={userInput.duration}
          min="1"
          max="50"
          step="1"
          onChange={(value) => onChange('duration', value)}
        />
      </div>
    </div>
  );
}
