import React from 'react';
import {
  LineChart,
  Line,
  BarChart,
  Bar,
  PieChart,
  Pie,
  Cell,
  AreaChart,
  Area,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  ResponsiveContainer,
} from 'recharts';

const COLORS = ['#8884d8', '#82ca9d', '#ffc658'];

export function InvestmentGraph({ data, graphType }) {
  return (
    <div style={{ width: '100%', height: 400 }}>
      <h3 className="center">Investment Growth Over Time</h3>
      <ResponsiveContainer>
        {graphType === 'LineChart' && (
          <LineChart
            data={data}
            margin={{ top: 20, right: 30, left: 20, bottom: 5 }}
          >
            <CartesianGrid strokeDasharray="3 3" />
            <XAxis dataKey="year" />
            <YAxis />
            <Tooltip formatter={(value) => `₹${value.toLocaleString()}`} />
            <Legend verticalAlign="top" />
            <Line type="monotone" dataKey="valueEndOfYear" stroke="#8884d8" name="Total Value" />
            <Line type="monotone" dataKey="interest" stroke="#82ca9d" name="Interest Earned" />
          </LineChart>
        )}
        {graphType === 'BarChart' && (
          <BarChart
            data={data}
            margin={{ top: 20, right: 30, left: 20, bottom: 5 }}
          >
            <CartesianGrid strokeDasharray="3 3" />
            <XAxis dataKey="year" />
            <YAxis />
            <Tooltip formatter={(value) => `₹${value.toLocaleString()}`} />
            <Legend verticalAlign="top" />
            <Bar dataKey="valueEndOfYear" fill="#8884d8" name="Total Value" />
            <Bar dataKey="interest" fill="#82ca9d" name="Interest Earned" />
          </BarChart>
        )}
        {graphType === 'PieChart' && (
          <PieChart>
            <Tooltip formatter={(value) => `₹${value.toLocaleString()}`} />
            <Legend verticalAlign="top" />
            <Pie
              data={data}
              dataKey="valueEndOfYear"
              nameKey="year"
              cx="50%"
              cy="50%"
              outerRadius={100}
              fill="#8884d8"
            >
              {data.map((entry, index) => (
                <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
              ))}
            </Pie>
          </PieChart>
        )}
        {graphType === 'AreaChart' && (
          <AreaChart
            data={data}
            margin={{ top: 20, right: 30, left: 20, bottom: 5 }}
          >
            <CartesianGrid strokeDasharray="3 3" />
            <XAxis dataKey="year" />
            <YAxis />
            <Tooltip formatter={(value) => `₹${value.toLocaleString()}`} />
            <Legend verticalAlign="top" />
            <Area
              type="monotone"
              dataKey="valueEndOfYear"
              stroke="#8884d8"
              fill="#8884d8"
              name="Total Value"
            />
          </AreaChart>
        )}
      </ResponsiveContainer>
    </div>
  );
}
