import React from 'react';
import { useLocation } from 'react-router-dom';
import './App.css';

const ConditionalCSSLoader = () => {
  const location = useLocation();

  React.useEffect(() => {
    if (location.pathname !== '/netflix') {
      import('./components/Navigation.css');
      import('./components/MobileNavigation.css');
      import('./App.css');
    }
  }, [location.pathname]);

  return null;
};

export default ConditionalCSSLoader;
